


@media (max-width: 576px) {
  .cont {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    /* flex-direction: column;   */
    align-items: center;
    color: black;
    font-size: 16px;
    font-family: Arial, Helvetica, sans-serif;
  }

  .logo {
   margin: 200px;
   margin-bottom: 1500px;
   align-self: center;
   height: 50%;
   width: 50%;
 
  }
    
   
  img {
    width: 100%;
  }
  
  .image-container{
   overflow: hidden;
   overflow-x: hidden;
  }
 
  .icon-container {
    padding: 5px; /* Reduce padding for smaller screens */

  }

  .icon-container span {
    margin-bottom: 5px; /* Reduce the space between each icon and text */
  }

  .icon-container i {
    margin-right: 5px; /* Reduce the space between icon and text */
    font-size: 20px; /* Reduce font size for smaller screens */
  }


}

